* {
  font-family: 'Roboto', Arial, sans-serif;
}

body {
  position: relative;
  min-width: 320px;
  color: #333;
  background: #fff;
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 14px;
  line-height: 1.3;
}

.main {
  padding-top: 20px;
  padding-bottom: 20px;
}

.inner {
  position: relative;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 1024px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.empty {
  margin-top: 100px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.3;
  text-align: center;
}

@media only screen and (max-width: 580px) {
  .empty {
    font-size: 24px;
  }
}

@media only screen and (max-width: 480px) {
  .empty {
    font-size: 22px;
  }
}

[hidden] {
  display: none !important;
}

[data-animation*="ripple"] {
  position: relative;
  overflow: hidden;
}

[data-animation*="ripple"] .ripple-circle {
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  width: 112px;
  height: 112px;
  background: rgba(0, 0, 0, 0.1);
  animation: ripple 0.5s linear forwards;
  pointer-events: none;
}

[data-animation*="white"] .ripple-circle {
  background: rgba(255, 255, 255, 0.1);
}

@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(2);
  }
}

.btn {
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  padding: 0 30px;
  min-width: 120px;
  border: none;
  outline: none;
  color: #333;
  background: #e9e9e9;
  font-size: 18px;
  font-weight: 300;
  line-height: 50px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  transition: opacity .175s, background .175s, box-shadow .175s;
}

.btn:disabled, .btn.is-disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.btn_primary {
  color: #fff;
  background: #2e99db;
}

.btn_primary:active {
  color: #fff;
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.btn_red {
  color: #fff;
  background: #ec4a41;
}

.btn_red:active {
  background: #e32117;
}

.btn_wide {
  width: 100%;
}

.input {
  padding: 0 12px;
  border: 1px solid #333;
  border-radius: 0;
  width: 100%;
  height: 50px;
  background: #fff;
  box-shadow: none;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  appearance: none;
  transition: border-color 0.25s;
}

.input::-ms-clear {
  display: none;
}

.input:not([readonly]):focus {
  border-color: #2e99db;
}

.input:disabled, .input[readonly] {
  border-color: #b3b3b3;
  background: #f2f2f2;
  cursor: not-allowed;
}

.input.is-error {
  border-color: #ec4a41;
}

.header {
  position: relative;
  height: 60px;
  color: #fff;
  background: #2e99db;
  text-align: center;
  font-size: 0;
}

.header:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

@media only screen and (max-width: 767px) {
  .header {
    padding-right: 0;
  }
}

.header__inner {
  display: inline-block;
  vertical-align: middle;
  padding-top: 5px;
  padding-bottom: 5px;
}

.header__title {
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
  text-transform: uppercase;
}

.list {
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
}

.list__item:not(:last-child) {
  margin-bottom: 5px;
}

.list__label, .list__value {
  display: inline-block;
  vertical-align: top;
}

.list__label {
  color: #b3b3b3;
}

.form {
  margin: 0 auto;
  max-width: 480px;
}

.form__item {
  display: flex;
}

@media only screen and (max-width: 480px) {
  .form__item {
    flex-direction: column;
  }
}

.form__item:not(:last-child) {
  padding-bottom: 30px;
}

.form__label {
  padding: 15px 15px 15px 0;
  width: 160px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  text-align: right;
}

@media only screen and (max-width: 480px) {
  .form__label {
    padding: 0 0 10px;
    text-align: left;
  }
}

.form__field {
  flex-grow: 1;
}

.form__btn {
  margin-top: 20px;
}

@media only screen and (max-width: 480px) {
  .form__btn {
    width: 100%;
  }
}

.form__list {
  padding-top: 15px;
}

@media only screen and (max-width: 480px) {
  .form__list {
    padding-top: 5px;
  }
}

.modal {
  position: fixed;
  z-index: 200;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  padding: 20px;
  opacity: 0;
  visibility: hidden;
  background: rgba(51, 51, 51, 0.8);
  text-align: center;
  font-size: 0;
  transition: opacity 0.25s, visibility 0.25s;
}

.modal:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
}

.modal.is-open .modal__overlay {
  pointer-events: auto;
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.modal__container {
  position: relative;
  z-index: 5;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  max-width: 400px;
  color: #333;
  background: #fff;
  box-shadow: 0 3px 7px 1px rgba(0, 0, 0, 0.1);
}

.modal__close {
  position: absolute;
  z-index: 2;
  top: 10px;
  right: 10px;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  color: #333;
  background: none;
  text-align: center;
  font-size: 0;
  transition: background 0.25s, box-shadow 0.25s;
}

.modal__close:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.no-touch .modal__close:hover {
  background: rgba(0, 0, 0, 0.05);
}

.modal__close:active {
  background: rgba(0, 0, 0, 0.1) !important;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2) !important;
}

.modal__close-icon {
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
  font-size: 18px;
}

.modal__body, .modal__footer {
  padding: 20px;
}

.modal__body {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
  text-align: left;
}

.modal__footer {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  text-align: right;
}

@media only screen and (max-width: 480px) {
  .modal__btn {
    width: 100%;
  }
}

.modal__btn:not(:last-child) {
  margin-right: 20px;
}

@media only screen and (max-width: 480px) {
  .modal__btn:not(:last-child) {
    margin: 0 0 10px;
  }
}
