.form {
	margin: 0 auto;
	max-width: 480px;
	&__item {
		display: flex;
		@include xs {
			flex-direction: column; }
		&:not(:last-child) {
			padding-bottom: 30px; } }
	&__label {
		padding: 15px 15px 15px 0;
		width: 160px;
		@include f(16px, 400, 1.25);
		text-align: right;
		@include xs {
			padding: 0 0 10px;
			text-align: left; } }
	&__field {
		flex-grow: 1; }
	&__btn {
		margin-top: 20px;
		@include xs {
			width: 100%; } }
	&__list {
		padding-top: 15px;
		@include xs {
			padding-top: 5px; } } }
